import React from 'react';
import SaleItem from "../SaleItem";
import {useSelector} from "react-redux";
import './HotProductNav.css'
const HotProductNav = () => {
    const hot_products = useSelector(state => state.prefetchDataReducer.hot_product)
    return (
        <section>
            <h3 className='hot-product-nav-left-product'>
                Sản phẩm nổi bật
            </h3>
            <div className='hot-product-nav-product-container'>
                {hot_products.map((item, index) => {
                    if (index > 4) return null
                    return <div style={{borderBottom: '1px solid rgba(0,0,0,0.1)', padding: '0.5em'}}>
                        <SaleItem data={item}/>
                    </div>
                })}
            </div>
        </section>
    );
};

export default HotProductNav;