import React from 'react';
import './SimilarProduct.css'
import {SlideTemplate} from "../../template/slide-template/SlideTemplate";
import ProductCard from "../../template/ProductCard";
const SimilarProduct = ({data}) => {
    const list_item = data.map((item, index) => {
        return <div className='similar-product-element' key={index}>
            <ProductCard data={item}/>
        </div>
    })
    return (
        <div className = 'similar-product-container'>
            <div className = 'similar-product-title'>SẢN PHẨM LIÊN QUAN</div>
            <hr className={'similar-product-divider'}></hr>
            <SlideTemplate data={list_item} isFixed={false} numDesktop={5} numMedium={3} numMobile={2}/>
        </div>
    );
};

export default SimilarProduct;