import React, {useEffect, useState} from "react";
import HeadProduct from "./Component/HeadProduct";
import BodyProduct from "./Component/BodyProduct";
import {useParams} from "react-router-dom"
import {getItems} from "../../api/api";
import './Product.css'
import SimilarProduct from "./Component/SimilarProduct";
import RouteTemplate from "../template/RouteTemplate";
import BodyTemplate from "../template/BodyTemplate";
import HotProductNav from "../template/HotProduct/HotProductNav";
import SkeletonTemplate from "../template/Skeleton/SkeletonTemplate";
import MetaTag from "../template/MetaTag/MetaTag";
import {APP_URL, DESC_DEFAULT, LOGO_DEFAULT} from "../../constants/Constants";
import ReactHtmlParser from 'react-html-parser'
import TawkComponent from "../../Tawk/TawkComponent";

export default function Product() {
    const [state, setState] = useState({
        count_click: 3,
        description: "",
        description_sale: "",
        name: "",
        price: 0,
        product_id: 0,
        sale_price: 0,
        remain: 0,
        specification: "",
        sub_category: {
            id: 0,
            name: '',
            path: '/',
        },
        category: {
            id: 0,
            name: '',
            path: '/',
        },
        url_image_1: null,
        url_image_2: null,
        url_image_3: null,
        url_image_4: null,
        url_image_main: null,
        similar_product: [],
        summary: ''
    })
    const [isLoading, setLoading] = useState(true)
    const {id} = useParams()
    useEffect(() => {
        setLoading(true)
        getItems(`/product/${id}`, {})
            .then(res => {
                setLoading(false)
                setState({
                    count_click: res.data.count_click,
                    description: res.data.description,
                    description_sale: res.data.description_sale,
                    name: res.data.name,
                    price: res.data.price,
                    product_id: res.data.product_id,
                    sale_price: res.data.sale_price,
                    specification: res.data.specification,
                    remain: res.data.remain,
                    sub_category: res.data.sub_category ? {
                        id: res.data.sub_category.id,
                        name: res.data.sub_category.name,
                        path: res.data.sub_category.path,
                    } : null,
                    category: {
                        id: res.data.category.id,
                        name: res.data.category.name,
                        path: res.data.category.path,
                    },
                    url_image_1: res.data.url_image_1,
                    url_image_2: res.data.url_image_2,
                    url_image_3: res.data.url_image_3,
                    url_image_4: res.data.url_image_4,
                    url_image_main: res.data.url_image_main,
                    similar_product: res.data.similar_product,
                    summary: res.data.description.substr(0, 200).replace(/(<([^>]+)>)/gi, "")
                })
            })
            .catch(error => console.log(error))
    }, [id])
    return (
        <BodyTemplate>
            {
                isLoading ? null : <MetaTag title={state.name} description={state.summary} url={APP_URL}
                                            image_url={state.url_image_main}/>
            }
            {
                isLoading ?
                    <div style={{marginBottom: '1em'}}>
                        <SkeletonTemplate
                            animation={true}
                            width="100%"
                            height="3em"
                            extraStyle={{marginTop: "1em", marginBottom: "1em"}}
                        />
                    </div> :
                    <RouteTemplate data={state.sub_category ?
                        [
                            {
                                name: 'Trang chủ',
                                path: '/'
                            },
                            {
                                name: state.category.name,
                                path: `/p/${state.category.path}`
                            },
                            {
                                name: state.sub_category.name,
                                path: `/c/${state.sub_category.path}`
                            },
                            {
                                name: state.name
                            }
                        ] : [
                            {
                                name: 'Trang chủ',
                                path: '/'
                            },
                            {
                                name: state.category.name,
                                path: `/p/${state.category.path}`
                            },
                            {
                                name: state.name
                            }
                        ]
                    }/>
            }
            <HeadProduct data={{
                images: {
                    url_image_main: state.url_image_main,
                    url_image_1: state.url_image_1,
                    url_image_2: state.url_image_2,
                    url_image_3: state.url_image_3,
                    url_image_4: state.url_image_4,
                },
                details: {
                    product_id: state.product_id,
                    name: state.name,
                    remain: state.remain,
                    price: state.price,
                    sale_price: state.sale_price,
                    description_sale: state.description_sale,
                    url_image_main: state.url_image_main,
                }
            }} isPreview={false} isLoading={isLoading}/>
            {
                isLoading ? null :
                    <section className='product-body'>
                        <BodyProduct data={{
                            product_id: state.product_id,
                            description: state.description,
                            specification: state.specification
                        }}/>
                        <div className='product-hot-product'>
                            <HotProductNav/>
                        </div>
                    </section>
            }
            {
                isLoading ? null :
                    <SimilarProduct data={state.similar_product}/>
            }
        </BodyTemplate>
    );
}
