import React, {useRef} from 'react';
import ReactHtmlParser from 'react-html-parser'
import "../../../../assets/css/content-style.css"
import "./Description.css"
import TableOfContent from "../../../template/TableOfContent";

const Description = ({data}) => {
    const ref = useRef(null)
    return (
        <React.Fragment>
            <section className="ck-content style-1 style-2">
                <TableOfContent container_ref = {ref} product_id = {data.product_id}/>
                <div ref = {ref}>
                    {ReactHtmlParser(data.description)}
                </div>
            </section>
        </React.Fragment>
    );
};

export default Description;