import React, {useState} from 'react';
import './ProductCard.css'
import LinkTemplate from "./LinkTemplate";
import {addToCard, openCartDialog, openProductDialog} from "../../redux/actions/actions";
import {useDispatch} from "react-redux";
import {useHistory} from 'react-router-dom'

const ProductCard = ({data}) => {
    const dispatch = useDispatch()
    const history = useHistory()
    const handleAddToCard = (event) => {
        event.stopPropagation()
        event.preventDefault()
        dispatch(addToCard({
            product_id: data.product_id,
            amount: 1,
            price: data.price,
            sale_price: data.sale_price,
            url_image_main: data.url_image_main,
            name: data.name
        }))
        dispatch(openCartDialog())
        // history.push('/cart')
    }
    const handlePreview = (event) => {
        event.stopPropagation()
        event.preventDefault()
        dispatch(openProductDialog({
            images: {
                url_image_main: data.url_image_main,
                url_image_1: data.url_image_1,
                url_image_2: data.url_image_2,
                url_image_3: data.url_image_3,
                url_image_4: data.url_image_4,
            },
            details: {
                product_id: data.product_id,
                name: data.name,
                price: data.price,
                sale_price: data.sale_price,
                description_sale: data.description_sale,
                url_image_main: data.url_image_main,
            }
        }))
    }
    return (
        <React.Fragment>
            <div className='product-card-container'>
                <LinkTemplate path={`/product/${data.product_id}`} className='product-card-link'>
                    <div className='product-card-image-container'
                         style={{backgroundImage: `url(${data.url_image_main})`}}>
                        {/*<div className='product-card-background'></div>*/}
                        <div className='product-card-inner-text'>
                            Hello
                        </div>
                        {
                            data.price === -1 ? null :
                                <div className='product-card-toCard' onClick={handleAddToCard}>
                                    <span>Thêm vào giỏ hàng</span>
                                </div>
                        }
                        <div className={'product-card-preview'} onClick={handlePreview}>
                            <i className="fas fa-eye"></i>
                        </div>
                        {data.price !== data.sale_price && data.price !== -1 ?
                            <div
                                className={'product-card-sale-percent'}>
                                <span
                                    className={'product-card-sale-span'}>{`${(100 - data.sale_price / data.price * 100).toFixed()}%`}
                                </span>
                            </div> : null
                        }
                    </div>
                </LinkTemplate>
                <div className='product-card-detail'>
                    <LinkTemplate path={`/product/${data.product_id}`} className='product-card-link'>
                        <div className='product-card-name'>{data.name}</div>
                    </LinkTemplate>
                    <div className={'product-card-price-container'}>
                        <div>
                            <div className='product-card-price'>
                                {(data.price !== data.sale_price && data.price !== -1) ? `${Number(data.price).toLocaleString('en')}đ` : ''}
                            </div>
                            <div
                                className='product-card-sale_price'>{data.price === -1 ? `Liên hệ` : `${Number(data.sale_price).toLocaleString('en')}đ`}</div>
                        </div>
                    </div>
                    {/*<div className='product-card-amount'>*/}
                    {/*    {*/}
                    {/*        Number(data.remain) !== 0? 'Còn hàng' : 'Hết hàng'*/}
                    {/*    }*/}
                    {/*</div>*/}
                </div>
            </div>
        </React.Fragment>
    );
};

export default ProductCard;