import React, {useEffect, useState} from 'react';
import './TableOfContent.css'
import LoadingCircle from "./LoadingCircle";

const TableOfContent = ({container_ref, product_id, path}) => {
    const [state, setState] = useState({
        isLoading: true,
        list: []
    })
    const handleClick = (e, item) => {
        item.scrollIntoView({behavior: 'smooth', block: 'start'})
    }
    const [expand, setExpand] = useState(true)
    useEffect(() => {
        setState({
            ...state,
            isLoading: true
        })
        if (container_ref.current === null) return
        var load_toc = setTimeout(() => {
            let content = container_ref.current
            let array_list = Array.from(content.querySelectorAll('h2, h3'))
            let current_h2 = 0
            let current_h3 = 0
            let list_full = array_list.map((item, index) => {
                if (item.tagName === 'H2') {
                    current_h2 = current_h2 + 1
                    current_h3 = 0
                    return <p className='toc-h2'
                              onClick={(e) => handleClick(e, item)}>{current_h2 + '. ' + item.textContent}</p>
                } else {
                    current_h3 = current_h3 + 1
                    return <div className='toc-h3'
                                onClick={(e) => handleClick(e, item)}>{current_h2 + '.' + current_h3 + '. ' + item.textContent}</div>
                }
            })
            setState({
                isLoading: false,
                list: list_full
            })
        }, 1000)
        return (() => {
            clearTimeout(load_toc)
        })
    }, [product_id, path])
    return (
        <div className='table-of-content-container'>
            <div className='toc-title'>
                <i className="fas fa-list-ol"></i>
                <span>Nội dung</span>
                <div className='toc-right-btn-container' onClick={() => setExpand(!expand)}
                     style={{transform: expand ? 'rotate(0deg)' : 'rotate(-90deg)'}}>
                    <i className='fas fa-chevron-down icon-right'></i>
                </div>
            </div>
            {
               state.isLoading ?
                    <div style={{display : 'flex', justifyContent : 'center', marginTop : '1em'}}>
                        <LoadingCircle></LoadingCircle>
                    </div> :
                    expand ?
                        <div className='toc-detail'>
                            <hr className={'detail-head-divider'}/>
                            {state.list}
                        </div> : null
            }
        </div>
    );
};

export default TableOfContent;