import React, {useState} from 'react';
import './BodyProduct.css'
import clsx from "clsx";
import Description from "./BodyComponent/Description";
import Specification from "./BodyComponent/Specification";

const BodyProduct = ({data}) => {
    const [tab, setTab] = useState(0)
    const handleChange = (e, value) => {
        if (value === tab) return
        setTab(value)
    }
    return (
        <section className='body-product-container'>
            <div className='body-product-header'>
                <div className={clsx({'body-product-tab': true, 'active': tab === 0})} onClick={(e) => handleChange(e,0)}>
                    Chi tiết sản phẩm
                </div>
                <div className={clsx({'body-product-tab': true, 'active': tab === 1})}
                     onClick={(e) => handleChange(e,1)}>
                    Thông số kĩ thuật
                </div>
            </div>
            <div className={'body-product-details'}>
                {
                    tab === 0?<Description data = {{description: data.description,
                    product_id : data.product_id}}/> :<Specification data = {data.specification}/>
                }
            </div>
        </section>
    );
};

export default BodyProduct;